.sliderm3 {
  position: relative;
  user-select: none;
  width: 100%; }

.sliderm3__scale {
  position: absolute;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between; }

.sliderm3__range {
  height: inherit;
  position: absolute;
  border-radius: inherit;
  background-color: rgba(255, 255, 255, 0); }

.sliderm3__point-range {
  position: absolute; }

.sliderm3__point {
  position: absolute;
  border-radius: 50%; }

.sliderm3__hint {
  position: absolute;
  display: flex;
  align-items: center;
  padding: 2px 8px;
  border-radius: 4px;
  background-color: inherit; }

.sliderm3__arrow {
  position: absolute;
  content: "";
  transform: rotate(-45deg);
  z-index: -1;
  background-color: inherit; }
